const API_URL = 'http://localhost:8088/api/equip';
export const locations ={
  state: {
    locations: []
  },
  getters: {
    getLocations(state) {
      return state.locations
    }
  },
  actions: {
    fetchLocations(ctx) {
      axios
        .get(API_URL + "/getLocations")
        .then(response => {
          ctx.commit('updateLocations', response.data)
        })
    }
  },
  mutations: {
    updateLocations(state, locations) {
      state.locations = locations
    }
  }
}



