export const equip ={
  state: {
    equip: {
      name:'',
      serialNumber:'',
      location: {name:''}
    }
  },
  getters: {
    getCurrentEquip(state) {
      return state.equip
    }
  },
  actions: {
  },
  mutations: {
    setEquip(state, equip) {
      state.equip = equip
    }
  }
}



